<template>
  <div>
    <transition name="fade">
    <span class="popup"
          v-show="showPopup">
      <span class="border-right">
        <button v-for="(fontStyle, index) in fontStyles"
                :key="index"
                :class="{selected: fontStyle.selected}"
                @click="setStyle(fontStyle.style, pressButton = true)"><i :class="fontStyle.icon"></i></button>
        <!--        <button @click="setStyle('italic')"><i class="fas fa-italic"></i></button>-->
        <!--        <button @click="setStyle('underline')"><i class="fas fa-underline"></i></button>-->
        <!--        <button @click="setStyle('strikethrough')"><i class="fas fa-strikethrough"></i></button>-->
      </span>
      <span class="border-right"><button @click="setStyle('justifyLeft')"><i class="fas fa-align-left"></i></button>
        <button @click="setStyle('justifyCenter')"><i class="fas fa-align-center"></i></button>
        <button @click="setStyle('justifyRight')"><i class="fas fa-align-right"></i></button>
        <button @click="setStyle('justifyFull')"><i class="fas fa-align-justify"></i></button>
      </span>
      <span class="border-right">
        <button @click="setStyle('insertUnorderedList')"><i class="fas fa-list-ul"></i></button>
        <button @click="setStyle('insertOrderedList')"><i class="fas fa-list-ol"></i></button>
      </span>
      <span class="border-right">
        <button @click="setStyle('formatBlock', false, 'h4')"><i class="fas fa-heading"></i></button>
      </span>
      <span class="border-right">
        <button @click="decreaseFont">smaller</button>
        <button @click="increaseFont">bigger</button>
      </span>
      <span class="border-right">
        <button @click="createFormula" class="btn-md">
<!--          <span class="material-icons md">functions</span>-->
        math
        </button>
        <!--        <button @click="toggleRaw">RAW</button>-->
      </span>
      <span class="">
        <button @click="setStyle('undo')"><i class="fas fa-undo-alt"></i></button>
      </span>
    </span>
    </transition>
    <div :id="id"
         :placeholder="placeholder"
         :style="`height: calc(1.5em * ${rows} + .75rem + 2px); display: inline-block`"
         class="form-control editor"
         contenteditable
         @focusin="showPopup = true" @focusout="hideSelf"
         ref="textarea"
         @input="onInput"
         @change="onInput"
         @click="updateCursorPos"
         @keydown="updateCursorPos"
    ></div>

    <!--    <div>-->
    <!--      cursorPos-->
    <!--      <div>-->
    <!--        {{ cursorPos }}-->
    <!--      </div>-->
    <!--    </div>-->
    <!--    <br>-->

    <div>
      dataRaw
      <div>
        <textarea v-model="data"
                  cols="100"
                  rows="10"
        ></textarea>
      </div>
    </div>

    <transition name="fade">
      <div class="modal-mask" v-show="showModal">
        <div class="modal-wrapper">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title">{{ isInEditMode ? 'Редактировать Формулу' : 'Вставить Формулу' }}</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="showModal = false">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <input v-model="formula"
                       style="width: 100%"
                       @input="renderFormula"
                >
                <div style="font-weight: bolder; margin: .8rem 0 .4rem">Результат:</div>
                <div ref="formulaPreview" style="margin: .5rem 0;"></div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="showModal = false">Отмена</button>
                <button type="button" class="btn btn-primary"
                        v-if="isInEditMode"
                        @click="saveFormula"
                >Сохранить
                </button>
                <button type="button" class="btn btn-primary"
                        v-else
                        @click="addFormula"
                >Добавить в конец
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>


export default {
  name: 'wysiwygMath',
  props: {
    id: String,
    placeholder: String,
    rows: {
      type: Number,
      default: 5
    },
  },
  data: function () {
    return {
      html: 'qweqweqweqweqwe<span contenteditable="false" class="math"><mjx-container class="MathJax" jax="CHTML" role="presentation" style="font-size: 113.1%; position: relative;"><mjx-math class="MJX-TEX" aria-hidden="true"><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D465 TEX-I"></mjx-c></mjx-mi><mjx-mo class="mjx-n" space="4"><mjx-c class="mjx-c3D"></mjx-c></mjx-mo><mjx-texatom space="4" texclass="ORD"><mjx-mfrac><mjx-frac><mjx-num><mjx-nstrut></mjx-nstrut><mjx-mrow size="s"><mjx-mo class="mjx-n"><mjx-c class="mjx-c2212"></mjx-c></mjx-mo><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44F TEX-I"></mjx-c></mjx-mi><mjx-mo class="mjx-n"><mjx-c class="mjx-cB1"></mjx-c></mjx-mo><mjx-msqrt><mjx-sqrt><mjx-surd><mjx-mo class="mjx-n"><mjx-c class="mjx-c221A"></mjx-c></mjx-mo></mjx-surd><mjx-box style="padding-top: 0.087em;"><mjx-msup><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44F TEX-I"></mjx-c></mjx-mi><mjx-script style="vertical-align: 0.289em;"><mjx-mn class="mjx-n" size="s"><mjx-c class="mjx-c32"></mjx-c></mjx-mn></mjx-script></mjx-msup><mjx-mo class="mjx-n"><mjx-c class="mjx-c2212"></mjx-c></mjx-mo><mjx-mn class="mjx-n"><mjx-c class="mjx-c34"></mjx-c></mjx-mn><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44E TEX-I"></mjx-c></mjx-mi><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D450 TEX-I"></mjx-c></mjx-mi></mjx-box></mjx-sqrt></mjx-msqrt></mjx-mrow></mjx-num><mjx-dbox><mjx-dtable><mjx-line></mjx-line><mjx-row><mjx-den><mjx-dstrut></mjx-dstrut><mjx-mrow size="s"><mjx-mn class="mjx-n"><mjx-c class="mjx-c32"></mjx-c></mjx-mn><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44E TEX-I"></mjx-c></mjx-mi></mjx-mrow></mjx-den></mjx-row></mjx-dtable></mjx-dbox></mjx-frac></mjx-mfrac></mjx-texatom></mjx-math><mjx-assistive-mml role="presentation" unselectable="on" display="inline"><mjx-container class="MathJax" jax="CHTML" role="presentation" style="font-size: 113.1%; position: relative;"><mjx-math class="MJX-TEX" aria-hidden="true"><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D465 TEX-I"></mjx-c></mjx-mi><mjx-mo class="mjx-n" space="4"><mjx-c class="mjx-c3D"></mjx-c></mjx-mo><mjx-mrow space="4"><mjx-mfrac><mjx-frac><mjx-num><mjx-nstrut></mjx-nstrut><mjx-mrow size="s"><mjx-mo class="mjx-n"><mjx-c class="mjx-c2212"></mjx-c></mjx-mo><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44F TEX-I"></mjx-c></mjx-mi><mjx-mo class="mjx-n"><mjx-c class="mjx-cB1"></mjx-c></mjx-mo><mjx-msqrt><mjx-sqrt><mjx-surd><mjx-mo class="mjx-n"><mjx-c class="mjx-c221A"></mjx-c></mjx-mo></mjx-surd><mjx-box style="padding-top: 0.087em;"><mjx-msup><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44F TEX-I"></mjx-c></mjx-mi><mjx-script style="vertical-align: 0.289em;"><mjx-mn class="mjx-n" size="s"><mjx-c class="mjx-c32"></mjx-c></mjx-mn></mjx-script></mjx-msup><mjx-mo class="mjx-n"><mjx-c class="mjx-c2212"></mjx-c></mjx-mo><mjx-mn class="mjx-n"><mjx-c class="mjx-c34"></mjx-c></mjx-mn><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44E TEX-I"></mjx-c></mjx-mi><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D450 TEX-I"></mjx-c></mjx-mi></mjx-box></mjx-sqrt></mjx-msqrt></mjx-mrow></mjx-num><mjx-dbox><mjx-dtable><mjx-line></mjx-line><mjx-row><mjx-den><mjx-dstrut></mjx-dstrut><mjx-mrow size="s"><mjx-mn class="mjx-n"><mjx-c class="mjx-c32"></mjx-c></mjx-mn><mjx-mi class="mjx-i"><mjx-c class="mjx-c1D44E TEX-I"></mjx-c></mjx-mi></mjx-mrow></mjx-den></mjx-row></mjx-dtable></mjx-dbox></mjx-frac></mjx-mfrac></mjx-mrow></mjx-math><mjx-assistive-mml role="presentation" unselectable="on" display="inline"><math xmlns="http://www.w3.org/1998/Math/MathML"><mi>x</mi><mo>=</mo><mrow><mfrac><mrow><mo>−</mo><mi>b</mi><mo>±</mo><msqrt><msup><mi>b</mi><mn>2</mn></msup><mo>−</mo><mn>4</mn><mi>a</mi><mi>c</mi></msqrt></mrow><mrow><mn>2</mn><mi>a</mi></mrow></mfrac></mrow></math></mjx-assistive-mml></mjx-container></mjx-assistive-mml></mjx-container></span><br>',

      showPopup: false,
      hideTimeout: null,
      formula: '\\(x = {-b \\pm \\sqrt{b^2-4ac} \\over 2a}\\)',

      data: '',
      cursorPos: 0,

      fontStyles: [
        {tag: 'b', icon: 'fas fa-bold', style: 'bold', selected: false},
        {tag: 'i', icon: 'fas fa-italic', style: 'italic', selected: false},
        {tag: 'u', icon: 'fas fa-underline', style: 'underline', selected: false},
        {tag: 'strike', icon: 'fas fa-strikethrough', style: 'strikethrough', selected: false},
      ],

      fontSizes: [
        {name: 'x-small', value: '1'},
        {name: 'small', value: '2'},
        {name: 'medium', value: '3'},
        {name: 'large', value: '4'},
        {name: 'x-large', value: '5'},
        {name: 'xx-large', value: '6'},
        {name: 'xxx-large', value: '7'},
      ],

      showModal: false,
      isInEditMode: false,
    }
  },
  watch: {
    cursorPos() {
      this.analyze();
    },
  },
  methods: {
    preventHide() {
      if (this.hideTimeout)
        clearInterval(this.hideTimeout);
    },

    setStyle(style, showUI = false, value = null, pressButton = false) {
      this.preventHide();
      document.execCommand(style, showUI, value);

      if (pressButton) {
        let fontStyle = this.fontStyles.find(fontStyle => fontStyle.style === style)
        if (fontStyle) {
          fontStyle.selected = true;
        }
      }

      // editor.trigger('keyup')
    },
    hideSelf() {
      this.hideTimeout = setTimeout(() => {
        this.showPopup = false;
      }, 200)
    },
    createFormula() {
      this.isInEditMode = false;
      this.showModal = true;
      this.renderFormula();
    },

    renderFormula() {
      this.$refs.formulaPreview.innerHTML = this.formula;
      setTimeout(window.MathJax.typeset, 200);
    },

    onInput(event) {
      this.$emit('input', event.target.innerHTML)
      this.data = event.target.innerHTML;
    },

    // editFormula() {
    //   this.isInEditMode = true;
    //   this.showModal = true;
    // },

    // handleInput(e) {
    //   this.divData = e.target.innerHTML;
    // },

    getCursorPos() {
      let _range = document.getSelection().getRangeAt(0)
      let range = _range.cloneRange()
      range.selectNodeContents(this.$refs.textarea)
      range.setEnd(_range.endContainer, _range.endOffset)
      return range.toString().length;
    },
    updateCursorPos() {
      this.cursorPos = this.getCursorPos();
    },

    analyze() {
      let selection = window.getSelection(), parentEl, tags = [];
      if (selection.rangeCount) {
        parentEl = selection.getRangeAt(0).commonAncestorContainer;

        // eslint-disable-next-line no-constant-condition
        while (true) {
          parentEl = parentEl.parentNode;

          if (parentEl == null)
            break;

          let parentName = parentEl.nodeName.toLowerCase();
          if (parentEl === this.$refs.textarea) {
            break;
          }
          tags.push(parentName);
        }
      }
      this.fontStyles.forEach(fontStyle => fontStyle.selected = tags.includes(fontStyle.tag));
    },

    // preventEnter(event) {
    //   if (event.keyCode === 13) {
    //     console.log('insertHTML')
    //     document.execCommand('insertHTML', false, '<br>');
    //     event.preventDefault();
    //     return false
    //   }
    // },

    increaseFont() {
      this.preventHide();

      let parent = window.getSelection().getRangeAt(0).commonAncestorContainer.parentElement
      if (parent !== this.$refs.textarea) {
        if (parent.style.fontSize !== '') {
          let index = this.fontSizes.map(obj => obj.name).indexOf(parent.style.fontSize);
          if (index < this.fontSizes.length - 1) {
            document.execCommand('styleWithCSS', false, true);
            document.execCommand('fontSize', false, this.fontSizes[index + 1].value);
            document.execCommand('styleWithCSS', false, false);
          }
        }
      } else {
        document.execCommand('styleWithCSS', false, true);
        document.execCommand('fontSize', false, this.fontSizes[3].value);
        document.execCommand('styleWithCSS', false, false);
      }
    },

    decreaseFont() {
      this.preventHide();

      let parent = window.getSelection().getRangeAt(0).commonAncestorContainer.parentElement
      if (parent !== this.$refs.textarea) {
        if (parent.style.fontSize !== '') {
          let index = this.fontSizes.map(obj => obj.name).indexOf(parent.style.fontSize);
          if (index > 0) {
            document.execCommand('styleWithCSS', false, true);
            document.execCommand('fontSize', false, this.fontSizes[index - 1].value);
            document.execCommand('styleWithCSS', false, false);
          }
        }
      } else {
        document.execCommand('styleWithCSS', false, true);
        document.execCommand('fontSize', false, this.fontSizes[1].value);
        document.execCommand('styleWithCSS', false, false);
      }
    },

    addFormula() {
      // let cursorPos = this.getCursorPos();

      // let formula = '\\(x = {-b \\pm \\sqrt{b^2-4ac} \\over 2a}\\)';

      // let aaa = this.$refs.textarea.innerHTML;

      // this.divData = aaa.slice(0, cursorPos) + formula + aaa.slice(cursorPos);
      // this.divData = aaa.slice(0, cursorPos) + `<span contenteditable="false" @click="editFormula">${formula}</span>` + aaa.slice(cursorPos);

      // this.$refs.textarea.innerHTML = this.$refs.textarea.innerHTML + formula + '<br>';
      // this.divData = this.$refs.textarea.innerHTML + `<span contenteditable="false" @click="editFormula">${formula}</span>`;

      // this.$refs.textarea.innerHTML = this.$refs.textarea.innerHTML + `<span contenteditable="false" @click="editFormula">${formula}</span>`;

      let data = '<span contenteditable="false" class="math">' + this.$refs.formulaPreview.innerHTML + '</span><br>';

      let content = this.$refs.textarea.innerHTML;
      // this.$refs.textarea.innerHTML = content.substring(0, content.length - 4) + data;
      this.$refs.textarea.innerHTML = content.substring(0, content.length - 4) + data + '<br>';
      this.data += data;

      setTimeout(window.MathJax.typeset, 200);

      this.showModal = false;
      this.formula = '';
    },
    saveFormula() {
    },
    deleteFormula() {
    },
  },
  beforeCreate() {
    let jScript = document.createElement('script')
    jScript.setAttribute('src', 'https://cdn.jsdelivr.net/npm/mathjax@3/es5/tex-mml-chtml.js')
    document.head.appendChild(jScript);
  },
  beforeMount() {
    window.MathJax = {
      options: {
        enableMenu: false,
      }
    };
    // window.MathJax.Hub.Config({
    //   showMathMenu: false,
    // });
  }
}
</script>

